@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  box-sizing: border-box;
  margin: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section{min-height:100vh}body{background-color:black;margin:0;padding:0}.center{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.center span{color:white;font-family:sans-serif;-webkit-animation:animate 1s ease infinite alternate;animation:animate 1s ease infinite alternate}.center span:nth-child(1){-webkit-animation-delay:0.2s;animation-delay:0.2s}.center span:nth-child(2){-webkit-animation-delay:0.4s;animation-delay:0.4s}.center span:nth-child(3){-webkit-animation-delay:0.6s;animation-delay:0.6s}.center span:nth-child(4){-webkit-animation-delay:0.8s;animation-delay:0.8s}.center span:nth-child(5){-webkit-animation-delay:1s;animation-delay:1s}.center span:nth-child(6){-webkit-animation-delay:1.2s;animation-delay:1.2s}.center span:nth-child(7){-webkit-animation-delay:1.4s;animation-delay:1.4s}@-webkit-keyframes animate{from{font-size:1em}to{font-size:4em}}@keyframes animate{from{font-size:1em}to{font-size:4em}}header{position:absolute;left:0;right:0;border-bottom:1px solid #423940;z-index:999}header .navbar-container{display:flex;flex-wrap:wrap;align-items:center;justify-content:space-between;margin:0 5rem}header .navbar-container .company-name a{font-size:35px;line-height:75px;letter-spacing:5px;color:#fff;text-transform:uppercase;text-decoration:none;font-weight:bolder}header .navbar-container .navbar ul{list-style:none;padding:0;margin:0}header .navbar-container .navbar ul li{display:inline-block;margin-left:20px}header .navbar-container .navbar ul li a{font-size:15px;text-decoration:none;line-height:75px;color:#fff;font-weight:bolder;display:block;position:relative}header .navbar-container .navbar ul li a.active::before{content:'';height:3px;background-color:#fff;width:100%;position:absolute;left:0;bottom:-1px}.intro-wrapper{background-image:url(/static/media/home-background.4ccd0f39.jpg);background-position:center;background-repeat:no-repeat;background-size:cover}.intro-wrapper .intro-row{margin:0 5rem;min-height:100vh;display:flex;flex-wrap:wrap;align-items:center}.intro-wrapper .intro-row .intro-content{border-left:5px solid #fff;padding-left:30px;width:75%}.intro-wrapper .intro-row .intro-content h6,.intro-wrapper .intro-row .intro-content h1,.intro-wrapper .intro-row .intro-content h3{color:#fff;line-height:1.2;margin:10px 0;letter-spacing:5px}.intro-wrapper .intro-row .intro-content h6{font-size:25px;font-weight:500}.intro-wrapper .intro-row .intro-content h1{font-size:90px;font-weight:700}.intro-wrapper .intro-row .intro-content h3{font-size:35px;font-weight:600}

